-<template>
  <div class="loginNow animate__animated animate__fadeInUpBig animate__faster">
    <div class="loginNow-top">
     <van-icon name="cross" size="25px" @click="$router.go(-1)"/>
     <h2>id登录</h2>
    </div>
    <div class="loginNow-main">
        <h3>其他登录方式因为有网易易盾登录要在app内验证</h3>
        
        <span style="color:#aeaeae">
            <span style="color:black;font-size:17px">ID在哪?</span>
            <span style="color:red;font-size:15px">个人中心</span>=>
            <span style="color:red;font-size:15px">分享</span>=>
            <span style="color:red;font-size:15px">复制链接</span>=>
            <span style="color:red;font-size:15px">粘贴到任意地方即可看到自己的ID</span>=>
            
        </span>
        <div>
            <h3>ID</h3>
            <input type="text" autofocus="autofocus" placeholder="输入Id" v-model="myID">
        </div>
        
    </div>
    
    <h2 id="ok" @click="getID">提交登录</h2>
  </div>
</template>

<script>
import axios from 'axios'
import { mapMutations, mapState } from 'vuex'
export default {
data(){
    return {
       myID:''
    }
},
methods:{
    getID(){
        axios.get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/user/detail?uid=${this.myID}`)
        .then(res => {
            this.getUser(res.data)
        })
        axios.get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/user/playlist?uid=${this.myID}`)
        .then(res => {
            console.log(res);
            this.getUserPlayList(res.data.playlist)
        })
    },
    ...mapMutations(['getUser','getUserPlayList'])
},
computed:{
    ...mapState(['user'])
},
watch:{
    user:function(){
        if(this.user.code===200){
            this.$router.push({
                name:'LoGin'
            })
        }else{
            alert('Id不正确')
        }
    }
}
}
</script>

