-<template>
  <div class="loginNow animate__animated animate__fadeInUpBig animate__faster">
    <div class="loginNow-top">
     <van-icon name="cross" size="25px" @click="$router.go(-1)"/>
     <h2>验证码登录</h2>
    </div>
    <div class="loginNow-main">
        <h3>登录体验更多精彩</h3>
        <span style="color:#aeaeae">未注册手机号登陆后将自动创建账号</span>
        <div>
            <h3>+86</h3>
            <input type="text" autofocus="autofocus" placeholder="输入手机号" v-model="phone">
        </div>
        <div id="mark">
            <input type="text" placeholder="输入六位数验证码" v-model="mark"><span @click="getMark" style="font-size:12px;color:#4674c1">发送验证码</span>
        </div>
    </div>
    <span style="margin-top:30px;display:block;font-size:18px" @click="runID">>>id登陆(假登录)</span>
    <h2 id="ok" @click="ok">提交登录</h2>
  </div>
</template>

<script>
import axios from 'axios'
export default {
data(){
    return {
        phone:'',
        mark:''
    }
},
methods:{
    getMark(){
        axios.get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/captcha/sent?phone=${this.phone}`)
        .then(res => {
            if(res.data.message){
                alert(res.data.message)
                return
            }
            
        })
        .catch(err => {
            alert(err); 
        })
    },
    ok(){
        axios.get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/login/cellphone?phone=${this.phone}&captcha=${this.mark}`)
        .then(res => {
            console.log(res)
        })
        .catch(err => {
            alert('接口报错,请使用id登陆'+err) 
        })
    },
    runID(){
        this.$router.push({
            name:'LoGinId'
        })
    }
}
}
</script>

<style lang="less" scoped>
.loginNow{
    padding-top: 20px;
    padding-left: 15px;
    background: #f7f7f7;
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 10;
    #ok{
       display: block;
       border:1px solid black;
       margin-top: 50px;
       width: 100px;
       text-align: center;
    }
    .loginNow-top{
        display: flex;
        align-items: center;
        h2{
            margin-left:50px;
        }
    }
    .loginNow-main{
        display: flex;
        flex-direction: column;
        margin-top: 40px;
        input{
