import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home'
import SongSheet from '../views/SongSheet'
import Search from '../views/Search'
import SearchDetai from '../views/SearchDetai'
import ZongHe from '../views/Search/ZongHe'
import LoGin from '../components/Home/LoGin'
import LoGinNow from '../components/Home/LoGinNow'
import LoGinId from '../components/Home/LoGinId'
const routes = [
  { path: '/', redirect: '/home', meta: { title: '首页' } },  
  { path: '/SearchDetai', redirect: '/SearchDetai/ZongHe', meta: { title: '首页' } },  
  {
    path: '/home',
    component: Home,
    name: 'home',
    meta: { title: '首页' },
    children:[
      {
        path: '/home/LoGin',
        component: LoGin,
        name: 'LoGin',
        meta: { title: '登录' },
      },
      {
        path: '/home/LoGinNow',
        component: LoGinNow,
        name: 'LoGinNow',
        meta: { title: '验证码登录' },
      },
      {
        path: '/home/LoGinId',
        component: LoGinId,
        name: 'LoGinId',
        meta: { title: 'Id登录' },
      },
    ]
  },
  {
    path: '/songSheet/:id',
    component: SongSheet,
    name: 'songSheet',
    meta: { title: '歌单' },
  },
  {
    path: '/search',
    component: Search,
    name: 'Search',
    meta: { title: '搜索' },
  },
  
  {
    path: '/searchDetai',
    component: SearchDetai,
    name: 'SearchDetai',
    meta: { title: '搜索结果' },
    children:[
    {
      path: '/SearchDetai/ZongHe',
      component: ZongHe,
      name: 'ZongHe',
      meta: { title: '综合' },
    },
  ]
  },
  
    
  
  
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
      document.title = to.meta.title
  }
  next()
})

export default router
