-<template>
  <div class="HotTop" v-show="showTop">
    <div class="HotTop-top">
      <div>
        <h2 style="color: #3f4658">排行榜</h2>
        <van-icon name="arrow" size="16px" />
      </div>
      <van-popover
        v-model="showPopover"
        trigger="click"
        :actions="actions"
        @select="onSelect"
         placement="left-end"
      >
        <template #reference>
          <van-icon name="ellipsis" size="23px" />
        </template>
      </van-popover>
    </div>
    <div class="HotTop-main">
      <div class="main-left">
        <img :src="TopData.coverImgUrl" alt="" />
      </div>
      <div class="main-right">
        <li v-for="(item, i) in TopData.tracks" :key="i">
          <div>
            <span style="color: red">{{ i + 1 }}</span>
            <span>{{ item.first }}</span>
            <span> - {{ item.second }}</span>
          </div>
          <span id="babang">霸榜</span>
        </li>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { Toast } from 'vant';
export default {
  name: "IconList",
  data() {
    return {
      TopData: "",
       showPopover: false,
      // 通过 actions 属性来定义菜单选项
    actions: [{ text: '排行榜详情' },{text:'接口调用不了'},{text:'不是俺敷衍'}],
    showTop:true
    };
  },
  mounted() {
    axios.get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/toplist/detail`).then((res) => {
      
      this.TopData = res.data.list.filter((i) => {
        return i.tracks[0];
      });
      this.TopData = this.TopData[Math.floor(Math.random() * 4)];
      
    });
  },

  methods:{
   
    onSelect(action) {
      Toast(action.text);
      if(this.showPopover === true){
        this.showTop = false
      }
    },
  }
};
</script>

<style lang="less" scoped>
.HotTop {
  padding: 10px 0;
  margin: 0 10px;
  border-radius: 10px;
  .HotTop-top {
    margin: 0 2px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    div {
      display: flex;
      align-items: center;
    }
  }
  .HotTop-main {
    margin-top: 10px;
    padding: 14px 10px;
    background: white;
    border-radius: 10px;
    display: flex;

    .main-left {
      img {
        width: 80px;
        height: 80px;
      }
    }
    .main-right {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-left: 7px;
      width: 100%;
      li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        #babang {
          font-size: 5px;
          color: red;
          display: block;
          width: 30px;
          position: absolute;
          right: 10px;
        }
        div {
          display: flex;
          span:nth-child(2) {
            margin-left: 5px;
            color: #3f4658;
            font-size: 12px;
          }
          span:nth-child(3) {
            color: #7f838f;
            font-size: 12px;
            overflow: hidden;
            white-space: nowrap; //只保留一个空白，文本不会换行，会在在同一行上继续，直到遇到br标签为止。
            text-overflow: ellipsis;
            max-width: 85px;
            display: block;
          }
        }
      }
    }
  }
}
</style>