                <div class="main-list">
                    <li v-for="list in userPlayList" :key="list.id" @click="goSongSheet(list.id)">
                        <div class="list-left">
                            <img :src="list.coverImgUrl" alt="">
                        </div>
                        <div class="list-right">
                            <span>{{list.name}}</span>
                            <div>
                                <span>{{list.trackCount}}首</span>
                                <span>播放{{list.playCount}}次</span>
                            </div>
                        </div>
                    </li>
                </div>
            </div>
        </div>
   </div>
    <div class="rightBlack" @click="closeLoGin">
        
    </div>
</div> 
</template>

<script>
import { mapMutations, mapState } from 'vuex'
export default {
 computed:{
    ...mapState(['show','user','userPlayList'])
 },
 methods:{
    to(){
        this.$router.push({
            name:'LoGinNow'
        })
    },
    goSongSheet(id){
        this.$router.push({
            path:`/songSheet/${id}`
            
        })
    },
    ...mapMutations(['closeLoGin'])
 }
}
</script>

