-<template>
  <div class="Search">
  <div class="top">
      <div class="topSearch">
        <van-icon name="arrow-left" @click="back" color="black" size="20" />
        
        <input type="search" autofocus="autofocus" :placeholder="searchDefault" v-model="value" @keydown.enter="Enter1"/>
        <span @click="Enter1">搜索</span>
      </div>
  </div>
  <div class="showThink" v-show="thinks !== ''">
        <ul>
          <li v-for="item in thinks.slice(0,10)" :key="item.id" @click="value = item.name + item.album.name">
            <van-icon name="search" id="search" />
            <span class="van-ellipsis" id="songName">{{item.name}}</span>
            <span class="van-ellipsis">{{item.album.name}}</span>
          </li>
        </ul>
  </div>
  <!-- 搜索历史 -->
  <div class="searchHistory">
    <div class="searchHistory-top">
      搜索历史
      <svg t="1674044340911" @click="clear" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2310" width="4.5vw" height="4.5vw"><path d="M779.3 228.2h-113v-35.4c0-34.9-28.4-63.3-63.3-63.3H425c-34.9 0-63.3 28.4-63.3 63.3v35.4h-113c-32.9 0-59.7 26.8-59.7 59.7v38.5c0 32.9 26.8 59.7 59.7 59.7h1.8v412.8c0 54.1 44 98.1 98.1 98.1h330.9c54.1 0 98.1-44 98.1-98.1V386.1h1.8c32.9 0 59.7-26.8 59.7-59.7v-38.5c-0.1-32.9-26.8-59.7-59.8-59.7z m-374.9-35.4c0-11.4 9.2-20.6 20.6-20.6h178c11.4 0 20.6 9.2 20.6 20.6v35.4H404.4v-35.4z m330.4 606c0 30.5-24.8 55.4-55.4 55.4H348.5c-30.5 0-55.4-24.8-55.4-55.4V386.1h441.7v412.7z m61.5-472.4c0 9.4-7.6 17-17 17H248.7c-9.4 0-17-7.6-17-17v-38.5c0-9.4 7.6-17 17-17h530.7c9.4 0 17 7.6 17 17v38.5z" p-id="2311" fill="#8a8a8a"></path><path d="M377.9 462.3h42.7v317.5h-42.7zM492.6 462.3h42.7v317.5h-42.7zM607.4 462.3h42.7v317.5h-42.7z" p-id="2312" fill="#8a8a8a"></path></svg>
    </div>
    <div class="list">
      <li v-for="(item,i) in historyList" :key="i" @click="clickStory(item)">
      {{item}}
    </li>
    </div>
    
  </div>
  <!-- 热搜榜 -->
  <div class="hotTop">
    <div class="hotTop-top van-hairline--bottom">
      <span><h3>热搜榜</h3></span>
      <div id="play">
        <van-icon name="play" size="14px"/>
        播放
      </div>
    </div>
    <div class="hotTop-main">
      <ul>
        <li v-for="(item,i) in hotTop" :key="item.score" @click="clickHotTop(item.searchWord)">
          <span>{{i+1}}</span>
          <span>{{item.searchWord}}</span>
        </li>
      </ul>
    </div>
  </div>
  </div>
  
</template>

<script>
import axios from 'axios'
import { showToast } from 'vant';
import { mapMutations } from 'vuex';
export default {
  name: "Search",
  methods: {
    back() {
      this.$router.replace({ path: '/home' })
    },
    Enter1(){
      if(this.value == ''){
        		showToast('请输入搜索内容');
        		return
      }else{
        this.historyList.unshift(this.value)
        localStorage.setItem("history",JSON.stringify(this.historyList))
        //跳转到搜索详情
        this.$router.push({
          path:"/SearchDetai",
      
        })
      }
    },
    //点击搜索历史跳转
    clickStory(v){
      this.value = v
      
      this.$router.push({
          path:"/SearchDetai",
          
        })
    },
    //点击热搜榜跳转
    clickHotTop(v){
      this.value = v
      this.historyList.unshift(this.value)
        localStorage.setItem("history",JSON.stringify(this.historyList))
      this.$router.push({
          path:"/SearchDetai",
      })
    },
    clear(){
      localStorage.clear()
      this.historyList = []
    },
    ...mapMutations(['getValue'])
  },
  watch:{
   value:function(){
    axios.get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/search?keywords=${this.value}`).then(
        (res) => {
          if(this.value !== ''){
            this.thinks = res.data.result.songs
          }else {
            this.thinks = ''
          }
        }
    );
    this.getValue(this.value)
   }
  },
  data(){
     return {
        searchDefault:'',
        value:'',
        thinks:'',
        historyList:[],
        hotTop:[]
     }
  },
  beforeCreate() {
    document.querySelector("body").setAttribute("style", "background:#f6f6f6");
  },
  // 销毁节点之前就删除属性
  beforeDestroy() {
    document.querySelector("body").removeAttribute("style");
  },
  mounted(){
    //获取搜索默认关键字
    axios.get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/search/default`).then(
        (res) => {
          this.searchDefault = res.data.data.realkeyword
        }
    );
    //如果本地存储的数据historyList有值，直接赋值给data中的historyList
    if (localStorage.getItem("history")) {
        this.historyList = JSON.parse(localStorage.getItem("history")) ;
    };
    //获取热搜榜
    axios.get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/search/hot/detail`).then(
        (res) => {
          this.hotTop = res.data.data
        }
    );
  }
};
</script>

<style lang="less" scoped>
input::input-placeholder{
	color:#c4c4c5;
}
.Search {
  .top {
    width: 100%;
    padding: 10px 15px;
    background: #fefefe;
    .topSearch {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 16px;
      #search {
        position: absolute;
        left: 52px;
      }
      input {
        border: none;
        border-radius: 15px;
        width: 250px;
        height: 30px;
        background: #f5f5f5;
        padding-left: 33px;
        font-size: 14px;
        padding-right: 5px;
      }
    }
  }
  .showThink{
    background: #fefefe;
    width: 100%;
    padding: 0 0 0 10px;
    ul{
      li{
        height: 40px;
        display: flex;
        align-items: center;
        #songName{
          padding-left: 10px;
          color: #9b9b9b;
        }
      }
    }
  }
  

  .searchHistory{
    margin-top: 30px;
    padding: 0 15px;
    .searchHistory-top{
      display: flex;
      justify-content: space-between;
      font-size: 14px;
      font-weight: 600;
      align-items: center;
    }
  }
  .list{
    padding: 10px 0;
    display: flex;
    align-content: space-between;
    flex-wrap:wrap;
    margin: 0 -5px -10px 0;
    
    li{
      background: #ffffff;
      padding: 2px 10px;
      border-radius: 15px;
      margin: 0 5px 10px 0;
      font-size: 14px;
      display: flex;
      justify-content: center;
      
    }
    // li:nth-of-type(n+2){
    //   margin-left: 8px;
    // }
  }
 
 
  .hotTop{
    margin: 10px 0 0 15px;
    background: #fefefe;
    padding: 0 15px 15px 15px;
    border-radius: 10px;
    width: 250px;
    .hotTop-top{
      display: flex;
      height: 40px;
      align-items: center;
      #play{
        background: #f6f6f6;
        border-radius: 15px;
        width: 50px;
        padding: 0 5px;
        font-size: 12px;
        display: flex;
        align-items: center;
        margin-left: 6px;
        justify-content: center;
        height: 23px;
      }
      
    }
    .hotTop-main{
        margin-top: 10px;
        span:nth-child(1){
          font-size: 17px;
          color: #707070;
          width: 15px;
          display: block;
          
        }
        ul>li:nth-child(-n+3){
          span:nth-child(1){
            color: red;
          }
        }
        li{
          display: flex;
          align-items: center;
          height: 29px;
        }
        span:nth-child(2){
          margin-left: 7px;
        }
    }
  }
}
</style>