<script>
import { mapMutations, mapState } from "vuex";
import axios from "axios";
export default {
  mounted() {
    this.searchValue = this.value;
    axios
      .get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/search/multimatch?keywords=${this.searchValue}`)
      .then((res) => {
       
        if(res.data.result.artist){
          this.searchRes1 = res.data.result.artist[0]
        }else{
          return
        }
        
        if(res.data.result.playlist){
          this.searchRes2 = res.data.result.playlist[0]
        }else{
          return
        }
        
      });

      axios
     .get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/cloudsearch?keywords=${this.searchValue}`)
      .then((res) => {
        this.searchSongs = res.data.result.songs
        
      });
     
  },
  computed: {
    ...mapState(["value"]),
  },
  data() {
    return {
      searchValue: "",
      searchRes1: [],
      searchRes2:[],
      searchSongs:[]
    };
  },
  watch: {
    value:function(){
      this.searchValue = this.value
      axios
      .get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/search/multimatch?keywords=${this.searchValue}`)
      .then((res) => {
       
        if(res.data.result.artist){
          this.searchRes1 = res.data.result.artist[0]
        }else{
          return
        }
        
        if(res.data.result.playlist){
          this.searchRes2 = res.data.result.playlist[0]
        }else{
          return
        }
        
      });

      axios
     .get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/cloudsearch?keywords=${this.searchValue}`)
      .then((res) => {
        this.searchSongs = res.data.result.songs
        
      });
    }
  },
  methods:{
    upDATA(v){
      this.upPlayList(this.searchSongs)
      this.upIndex(v)
    },
    
     ...mapMutations(['upPlayList','upIndex'])
  }
};
</script>

