import { createStore } from 'vuex'
import { createApp } from 'vue';
import { Toast } from 'vant';
import { showToast } from 'vant';
const app = createApp();
app.use(Toast);


export default createStore({
  state: {
    playList:[{
      al:{
        id:'387585',
        name:'Bye Bye Disco',
        picUrl:'https://p2.music.126.net/5sJbknBKzlhOe1l4IBxJMw==/109951166567445610.jpg?param=34y34',
      },
      ar:[{
        name:'新裤子',
      }],
      name:'Bye Bye Disco',
      id:'387585',
    }
      
    ],
    index:0,
    value:'',
    showLOGIN:false,
    show:false,
    user:{},
    userPlayList:[]
  },
  getters: {
  },
  mutations: {
    upPlayList(state,value){
     
      state.playList = value
    },
    upIndex(state,value){
      state.index = value
    },
    nextSong(state,value){
      if(state.playList.length <= 1){
        showToast('最后一首啦');
        return
      }
      state.index++
      
    },
    leftSong(state,value){
      if(state.playList.length <= 1){
        showToast('最后一首啦');
        return
      }
      state.index--
    },
    //删除播放列表歌曲
    deleteSong(state,value){
      state.playList.splice(value,1)
    },
    //获取搜索value
    getValue(state,value){
      state.value = value
    },
    //显示登录页
    showLoGin(state,value){
      if(state.showLOGIN === false){
        state.showLOGIN = true
       
      }else{
        state.showLOGIN = false
        
      }
    },
    //关闭登录页/遮罩层
    closeLoGin(state,value){
      
      state.showLOGIN =false
    },
    //获取用户星系
    getUser(state,value){
      state.user = value
    },
    //获取用户歌单
    getUserPlayList(state,value){
      state.userPlayList = value
    }
  },
  actions: {
  },
  modules: {
  }
})
