-<template>
  <div class="footPlay">
        <img id="alPic" :src="playList[index].al.picUrl" alt="" @click="showPopup" :class="{'clockwise-rotate':show1===true}">
        <div class="msg">
            <span id="name">{{playList[index].name}}</span> 
            <span id="ar"> - {{playList[index].ar[0].name}}</span>
        </div>
        
        <audio  ref="audio"  :src="`https://music.163.com/song/media/outer/url?id=${playList[index].id}.mp3`"></audio>
        <div class="playBtn">
            <van-icon v-show="show2" name="play" size="27" @click="play"/>
            <van-icon v-show="show1" name="pause" size="27" @click="stop" />
            <van-icon @click="showSongList" name="bars" size="27" style="padding-left:10px" />
        </div>
        <van-popup
            v-model:show="showPop"
            closeable
            close-icon-position="top-left"
            close-icon="arrow-down"
            position="bottom"
            :style="{ height: '100%',background:'#000' }"
        >
            <img id="songBkg" :src="playList[index].al.picUrl" alt="">
            <div class="songTop">
                <div class="msg2">
                    <span>{{playList[index].name}}</span>
                    <span id="ar2">{{playList[index].ar[0].name}}</span>
                </div>
            </div>
            <img id="songArPic" :src="playList[index].al.picUrl" alt="" :class="{'clockwise-rotate':show1===true}">  
            <input type="range" step="0.05" v-model="audio.currentTime" min="0" :max="audio.duration">
           
            <div class="playList">
                <svg t="1673606155610" @click="leftSong" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1899" width="40" height="40"><path d="M364.302083 465.602819L687.954365 218.588294c38.416414-29.327534 93.791393-1.929039 93.791392 46.396277v494.029051c0 48.325316-55.374979 75.725617-93.791392 46.398084L364.302083 558.397181c-30.600916-23.357989-30.600916-69.436372 0-92.794362zM238.945254 780.798397V451.684117v-164.562559c0-19.628152-5.904521-60.475733 17.057907-75.841215 25.523642-17.068744 59.747828 1.210165 59.747828 31.919454v493.676839c0 19.628152 5.915358 60.473927-17.047069 75.841215-25.53448 17.068744-59.758666-1.211971-59.758666-31.919454z" fill="#ffffff" p-id="1900"></path></svg>
                <van-icon v-show="show2" name="play" size="50" @click="play" color="white"/>
                <van-icon v-show="show1" name="pause" size="50" @click="stop" color="white"/>
                <svg @click="nextSong" t="1673604045414" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1271" width="40" height="40"><path d="M655.706179 465.602819L332.053897 218.588294c-38.414608-29.327534-93.791393-1.929039-93.791392 46.396277v494.029051c0 48.325316 55.376785 75.725617 93.791392 46.398084l323.652282-247.014525c30.602722-23.357989 30.602722-69.436372 0-92.794362zM781.064814 780.798397V451.684117v-164.562559c0-19.628152 5.904521-60.475733-17.057907-75.841215-25.523642-17.068744-59.747828 1.210165-59.747828 31.919454v493.676839c0 19.628152-5.915358 60.473927 17.047069 75.841215 25.532673 17.068744 59.758666-1.211971 59.758666-31.919454z" fill="#ffffff" p-id="1272"></path></svg>
            </div>
       </van-popup>
       <!-- 当前播放列表 -->
       <div class="nowSongList">
         <van-popup v-model:show="showSong" position="bottom" class="nowList"
       :style="{ height: '60%',background:'#fff',width:'94vw',left:'10px',bottom:'10px','border-radius':'15px'}"
       :overlay-style="{ opacity:'0.4'}"
       >
         <div class="listTop" >
            <h3>当前播放</h3>
            <span>({{playList.length}})</span>
         </div>
          <div class="songs">
            <li v-for="(song,i) in playList" :key="song.id" @click="upIndex(i)" :class="{'active':i === index}">
                <div>
                    <span>{{song.name}}</span>
                    <span :class="{'active':i === index,'songsAr':i !== index}">- {{song.ar[0].name}}</span>
                </div>
                 <van-icon name="cross" size="14" color="#949697" @click="deleteSong(i)"/>
            </li>
            
          </div>  
       </van-popup>
       </div>
       
</div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'

export default {
    name:'FootPlay',
    computed:{
        ...mapState(['playList','index'])
    },
    methods:{
       play(){
        this.$refs.audio.play()
        this.show1 = true
        this.show2 = false
       },
       stop(){
        this.$refs.audio.pause()
        this.show1 = false
        this. show2 = true
       },
       showPopup() {
        this.showPop = true;
       },
       showSongList() {
        this.showSong = true;
       },
        ...mapMutations(['nextSong','leftSong','upIndex','deleteSong'])
    },
    data(){
        return{
            show1:false,
            show2:true,
            showPop:false,
            showSong:false, //显示歌单
            audio:'',
            currentTime:'',
        }
    },
    watch: {
        index:function(){
            this.$refs.audio.autoplay = true
            this.show1 = true
            this.show2 = false
        },
        // showPop:function(){
        //     if(this.showPop === false){ 
        //         clearInterval(this.audioTime)
        //     }else{
        //         this.audioTime = setInterval(() => {
        //             this.currentTime = this.$refs.audio.currentTime
                   
        //             console.log(1);
        //         }, 300);    
        //     }
        // },
        currentTime:function(){
            if(this.currentTime == this.audio.duration)
               this.nextSong()
        }
    },
    mounted(){
        this.$nextTick(() => {
            this.audio = this.$refs.audio;
            this.value = this.$refs.audio.currentTime
        })    
    },
    
}
</script>

<style lang="less" scoped>
    
    .footPlay{
        z-index: 5;
        padding-top: 15px;
        width: 100%;
        height: 50px;
        background-color: #fdfdfd;
        position: fixed;
        bottom: 0px;
        padding-bottom: 30px;
        display: flex;
        align-items: center;
        .msg{
            margin-left: 10px;
            width: 200px;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
        }
        .playBtn{
            margin-left: 20px;
        }
        #alPic{
            width: 41px;
            height: 41px;
            border-radius: 50px;
            margin-left: 10px;
            box-shadow: 0 0 1px 5px rgb(58, 58, 58)
        }
        .clockwise-rotate{
            -moz-animation: spin 8s infinite linear; 
            -o-animation: spin 8s infinite linear; 
            -webkit-animation: spin 8s infinite linear; 
            animation: spin 8s infinite linear;
        }
        #name{
            font-size: 14px;
            font-weight: 600;
            color: #5f677e;
        }
        #ar{
            color: #949697;
            font-size: 14px;
        }
        .van-popup{
            background: #000;
            #songBkg{
                width: 100vw;
                height: 100%;
                position: absolute;
                filter: blur(40vw);
                z-index: -1;
                overflow: hidden;
            }
            .songTop{
                width: 100%;
                display: flex;
                justify-content: center;
                margin-top: 14px;
                .msg2{
                    color: white;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    width: 230px;
                    //超出一行省略号
                    overflow: hidden;
                    white-space: nowrap;
                    font-size: 15px;
                    
                    #ar2{
                        color: rgb(143, 143, 143);
                    }
                }
            }
            #songArPic{
                width: 190px;
                height: 190px;
                border-radius: 100px;
                box-shadow: 0 0 1px 20px rgb(58, 58, 58);
                position: fixed;
                left: 25%;
                top: 25%;
            }
            .playList{
                display: flex;
                align-items: center;
                justify-content: center;
                position: fixed;
                bottom: 30px;
                width: 100%;
                .van-icon{
                    padding-left: 20px;
                    padding-right: 20px;
                }
            }
            input{
                width: 94%;
                position: fixed;
                bottom: 100px;
                left: 3%;
                height: 3px;
            }
           
           
            

        }
        .nowSongList{
            overflow: hidden;
            .listTop{
                display: flex;
                padding: 10px 17px 0 17px;
                position: sticky;
                top: 0;
                background: rgb(255, 255, 255);
                width: 100%;
                height: 40px;
                z-index: 1;
                span{
                    color: rgb(153, 153, 153);
                    font-size: 12px;
                    text-align: center;
                    line-height: 24px;
                    padding-left: 3px;
                }
            }
            .songs{
                li{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    height: 11vw;
                    width: 100%;
                    padding: 0 17px 0 17px;
                    div{
                        width: 90%;
                        overflow: hidden;
                        text-overflow:ellipsis;
                        white-space: nowrap;
                        .songsAr{
                        color: #949697;
                        }
                    }
                    span:nth-child(1){
                        font-size: 15px;
                    }
                    span:nth-child(2){
                        font-size: 13px;
                        margin-left: 5px;
                        
                        position: relative;
                    }
                    
                }
                li:active{
                    background: #ebebeb;
                }
            }
        }
    }
    .active{
        color: rgb(226, 4, 4);
    }
        @-moz-keyframes spin{
            0%{-moz-transform:rotate(0)}
            100%{-moz-transform:rotate(359deg)}
        }
        @-webkit-keyframes spin{
            0%{-webkit-transform:rotate(0)}
            100%{-webkit-transform:rotate(359deg)}
        }
        @-o-keyframes spin{
            0%{-o-transform:rotate(0)}
            100%{-o-transform:rotate(359deg)}
        }
        @-ms-keyframes spin{
            0%{-ms-transform:rotate(0)}
            100%{-ms-transform:rotate(359deg)}
        }
        @keyframes spin{
            0%{transform:rotate(0)}
            100%{transform:rotate(359deg)}
        }
</style>