  </div>
  <div class="songList">
      <div class="content">
        <div class="playAll">
          <span><svg t="1672807227671" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3647"><path d="M512 96C282.624 96 96 282.624 96 512s186.624 416 416 416 416-186.624 416-416S741.376 96 512 96z m0 768C317.92 864 160 706.08 160 512S317.92 160 512 160s352 157.92 352 352-157.92 352-352 352z" p-id="3648" fill="#d81e06"></path><path d="M466.816 324.96a32 32 0 0 0-50.816 25.888v339.776a32 32 0 0 0 50.816 25.856l233.6-169.888a32 32 0 0 0 0-51.776l-233.6-169.856z" p-id="3649" fill="#d81e06" data-spm-anchor-id="a313x.7781069.0.i11" class="selected"></path></svg>
          <h4>播放全部</h4>({{Song.length -1}})</span>
        </div>
      </div>
      <div class="songs">
        <ul>
            <li v-for="(song,index) in Song" :key="song.id" @click="upData(index)">
            <span>{{index+1}}</span>
            <div class="listRight">
              <span>{{song.name}}</span>
              <span id="author" style="color:#9ca0ab">{{song.ar[0].name}}</span>
            </div>
            </li>
        </ul>
      </div>  
      
  </div>
  
</div>
</template>

<script>
import axios from 'axios'
import { mapMutations } from 'vuex'
export default {
    name:'SongSheet',
    data(){
      return {
        sheetData:[],
        show:false,
        Song:[]
      }
    },
    mounted(){
      axios.get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/playlist/detail?id=${this.$route.params.id}`).then(
        (res) => {
          this.sheetData = res.data.playlist
        }
      )
      axios.get(`https://service-ryosij3a-1316619549.gz.apigw.tencentcs.com/release/playlist/track/all?id=${this.$route.params.id}&limit=40&offset=10`).then(
        (res) => {
          
          this.Song = res.data.songs
        }
      )
    },
    methods:{
      back(){
        this.$router.go(-1)
      },
      upData(index){
        this.upPlayList(this.Song)
        this.upIndex(index)
      },
      ...mapMutations(['upPlayList','upIndex'])
    }
}
</script>

<style lang="less" scoped>
